/* eslint-disable max-len */
/* eslint-disable @typescript-eslint/no-unused-expressions */
/* eslint-disable no-console */
import React, { useState, useEffect } from 'react';
import AppStyles from './App.module.css';
import AppHeader from '../appHeader/AppHeader';
import { order } from '../../utils/order';
import BurgerIngredients from '../burgerIngredients/BurgerIngredients';
import BurgerConstructor from '../burgerConstructor/BurgerConstructor';
import { apiConfig, parseResponse } from '../../utils/apiConfig';
import OrderDetails from '../orderDetails/OrderDetails';
import IngredientDetails from '../ingredientDetails/IngredientDetails';
import Modal from '../modal/Modal';

function App() {
  /*     'BurgerIngredients' */
  const [data, setData] = useState([]);
  const [isloading, setIsloading] = useState(true);
  const [hasError, setHasError] = useState(false);
  /*      (/)        */
  const [isOrderDetailsOpened, setIsOrderDetailsOpened] = useState(false);
  /*      (/)      */
  const [isIngredientDetailsOpened, setIsIngredientDetailsOpened] = useState(false);
  /*         */
  const [ingredient, setIngredient] = useState({});

  /*           "BurgerIngredients" */
  function getData() {
    // eslint-disable-next-line no-undef
    fetch(`${apiConfig.url}`)
      .then(parseResponse)
      .then((res) => {
        setData(res.data);
      })
      .catch(() => {
        setHasError(true);
      })
      .finally(() => {
        setIsloading(false);
      });
  }
  /*     ,        "getData" */
  useEffect(() => {
    getData();
  }, []);
  /*    */
  const closeAllModals = () => {
    setIsIngredientDetailsOpened(false);
    setIsOrderDetailsOpened(false);
    setHasError(false);
  };
  /*       "Escape" */
  const handleEscKeydown = (e) => {
    e.key === 'Escape' && closeAllModals();
  };
  /*       "x" */
  const handleCloseClick = (e) => {
    e.target && closeAllModals();
  };
  /*    ,         ,
  "item",   props   "BurgerIngredients" */
  const handleIngredientClick = (item) => {
    setIngredient(item);
    setIsIngredientDetailsOpened(true);
  };
  /*        */
  const handleOrderClick = () => {
    setIsOrderDetailsOpened(true);
  };
  /*    */
  return (
    <div className={AppStyles.app}>
      <AppHeader />
      <main className={`${AppStyles.main} mb-10`}>
        <BurgerIngredients data={data} onIngredientClick={handleIngredientClick} />
        <BurgerConstructor order={order} onOrderConfirmClick={handleOrderClick} />
      </main>

      {!isloading && hasError && (
        <Modal
          heading="-   ..."
          handleKeydown={handleEscKeydown}
          closeModal={handleCloseClick}
        />
      )}
      {isOrderDetailsOpened && (
        <Modal handleKeydown={handleEscKeydown} closeModal={handleCloseClick}>
          <OrderDetails />
        </Modal>
      )}
      {isIngredientDetailsOpened && (
        <Modal
          heading=" "
          handleKeydown={handleEscKeydown}
          closeModal={handleCloseClick}
        >
          <IngredientDetails ingredient={ingredient} />
        </Modal>
      )}

    </div>
  );
}

export default App;
